import { useDispatch, useSelector } from 'react-redux'
import { Link, Redirect } from 'react-router-dom'
import InputPasswordToggle from '@components/input-password-toggle'
import { Button, Card, CardBody, CardTitle, CustomInput, Form, FormGroup, Label } from 'reactstrap'
import '@styles/base/pages/page-auth.scss'

import UILoader from '@components/ui-loader'
import InputGroup from '@components/input-group'
import { login, loginChange } from '@store/Login/actions'

const Login = () => {
  const { authenticated } = useSelector(store => store.authentication),
    { loginFormData, formErrors, isLoading, isSubmitting } = useSelector(store => store.login),
    dispatch = useDispatch()

  if (authenticated) return <Redirect to='/dashboard' />

  const handleFormChange = (name, value) => {
    dispatch(loginChange(name, value))
  }

  const handleSubmit = event => {
    event.preventDefault()
    dispatch(login())
  }

  const _onChange = e => {
    handleFormChange(e.target.name, e.target.value)
  }

  return (
    <UILoader blocking={isLoading}>
      <div className='auth-wrapper auth-v1 px-2'>
        <div className='auth-inner py-2'>
          <Card className='mb-0'>
            <CardBody>
              <Link className='brand-logo' to='/' onClick={e => e.preventDefault()}>
                <h2 className='brand-text text-primary ml-1'>Carhoot</h2>
              </Link>
              <CardTitle tag="h4" className="mb-1">
                Login to your account
              </CardTitle>
              <Form className='auth-login-form mt-2' onSubmit={handleSubmit}>
                <InputGroup
                  autoComplete={'on'}
                  type={'email'}
                  label={'Email'}
                  error={formErrors['email']}
                  name={'email'}
                  defaultValue={loginFormData.email}
                  placeholder={'Please Enter Your Email'}
                  value={loginFormData.email}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                />
                <FormGroup>
                  <div className='d-flex justify-content-between'>
                    <Label className='form-label' for='login-password'>
                      Password
                    </Label>
                    <Link to='/forget'>
                      <small>Forgot Password?</small>
                    </Link>
                  </div>
                  <InputPasswordToggle
                    className='input-group-merge'
                    id='login-password'
                    name={'password'}
                    placeholder={'Please Enter Your Password'}
                    value={loginFormData.password}
                    autoComplete={'current-password'}
                    onChange={(e) => {
                      _onChange(e)
                    }}
                />
                <span className="text-danger">{formErrors['password'] && formErrors['password'][0]}</span>
              </FormGroup>
              <FormGroup>
                <CustomInput type='checkbox' className='custom-control-Primary' id='remember-me' label='Remember Me' />
              </FormGroup>
              <Button.Ripple color='primary' block type={'submit'} disabled={isSubmitting}>
                Sign in
              </Button.Ripple>
            </Form>
          </CardBody>
        </Card>
      </div>
    </div>
    </UILoader>
  )
}

export default Login
